`, "",{"version":3,"sources":["webpack://./src/views/WebhookSettings.vue"],"names":[],"mappings":";AAmHA;EACE,aAAa;EACb,yCAAyC;EACzC,gBAAgB;AAClB;AAEA;EACE,iBAAiB;EACjB,eAAe;EACf,kBAAkB;AACpB;AAEA;EACE,oCAAoC;EACpC,eAAe;EACf,mBAAmB;AACrB;AAEA;;EAEE,oCAAoC;EACpC,kBAAkB;EAClB,cAAc;AAChB;AAEA;EACE,mBAAmB;AACrB;AAEA;EACE,eAAe;AACjB;AAEA;EACE,sCAAsC;EACtC,gCAAgC;EAChC,YAAY;EACZ,yCAAyC;EACzC,iBAAiB;EACjB,eAAe;EACf,eAAe;AACjB;AAEA;EACE,4CAA4C;AAC9C","sourcesContent":["<template>\r\n  <div class=\"webhook-settings\">\r\n    <h4 class=\"webhook-settings__title\">\r\n       Discord Webhooks\r\n    </h4>\r\n\r\n    <p class=\"webhook-settings__description\">\r\n      Create webhooks that accept Discord-formatted JSON payloads\r\n      and post messages into this conversation.\r\n    </p>\r\n\r\n    <!-- Webhook list -->\r\n    <div v-if=\"loading\" class=\"webhook-settings__loading\">\r\n      Loading webhooks\r\n    </div>\r\n\r\n    <div v-else-if=\"webhooks.length === 0\" class=\"webhook-settings__empty\">\r\n      No webhooks configured yet.\r\n    </div>\r\n\r\n    <div v-else class=\"webhook-settings__list\">\r\n      <WebhookItem\r\n          v-for=\"webhook in webhooks\"\r\n          :key=\"webhook.id\"\r\n          :webhook=\"webhook\"\r\n          @delete=\"onDelete\" />\r\n    </div>\r\n\r\n    <!-- Create form toggle -->\r\n    <div v-if=\"!showCreateForm\" class=\"webhook-settings__actions\">\r\n      <button class=\"primary\" @click=\"showCreateForm = true\">\r\n        + Create webhook\r\n      </button>\r\n    </div>\r\n\r\n    <WebhookCreateForm\r\n        v-if=\"showCreateForm\"\r\n        :conversation-token=\"conversationToken\"\r\n        @created=\"onCreated\"\r\n        @cancel=\"showCreateForm = false\" />\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport axios from '@nextcloud/axios'\r\nimport { generateUrl } from '@nextcloud/router'\r\nimport WebhookItem from '../components/WebhookItem.vue'\r\nimport WebhookCreateForm from '../components/WebhookCreateForm.vue'\r\n\r\nexport default {\r\n  name: 'WebhookSettings',\r\n\r\n  components: {\r\n    WebhookItem,\r\n    WebhookCreateForm,\r\n  },\r\n\r\n  props: {\r\n    conversationToken: {\r\n      type: String,\r\n      required: true,\r\n    },\r\n  },\r\n\r\n  data() {\r\n    return {\r\n      webhooks: [],\r\n      loading: true,\r\n      showCreateForm: false,\r\n    }\r\n  },\r\n\r\n  mounted() {\r\n    this.fetchWebhooks();\r\n    console.log('Mounted')\r\n  },\r\n\r\n  methods: {\r\n    async fetchWebhooks() {\r\n      this.loading = true\r\n      try {\r\n        const url = generateUrl('/apps/talk_discord_webhook/api/v1/webhooks/conversation/{conversationToken}', {\r\n          conversationToken: this.conversationToken,\r\n        })\r\n        const response = await axios.get(url)\r\n        this.webhooks = response.data\r\n      } catch (error) {\r\n        console.error('Failed to load webhooks', error)\r\n        this.webhooks = []\r\n      } finally {\r\n        this.loading = false\r\n      }\r\n    },\r\n\r\n    onCreated(webhook) {\r\n      this.webhooks.unshift(webhook)\r\n      this.showCreateForm = false\r\n    },\r\n\r\n    async onDelete(webhookId) {\r\n      try {\r\n        const url = generateUrl('/apps/talk_discord_webhook/api/v1/webhooks/{id}', {\r\n          id: webhookId,\r\n        })\r\n        await axios.delete(url)\r\n        this.webhooks = this.webhooks.filter(w => w.id !== webhookId)\r\n      } catch (error) {\r\n        console.error('Failed to delete webhook', error)\r\n      }\r\n    },\r\n  },\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.webhook-settings {\r\n  padding: 16px;\r\n  border-top: 1px solid var(--color-border);\r\n  margin-top: 12px;\r\n}\r\n\r\n.webhook-settings__title {\r\n  font-weight: bold;\r\n  font-size: 16px;\r\n  margin-bottom: 8px;\r\n}\r\n\r\n.webhook-settings__description {\r\n  color: var(--color-text-maxcontrast);\r\n  font-size: 13px;\r\n  margin-bottom: 12px;\r\n}\r\n\r\n.webhook-settings__loading,\r\n.webhook-settings__empty {\r\n  color: var(--color-text-maxcontrast);\r\n  font-style: italic;\r\n  padding: 8px 0;\r\n}\r\n\r\n.webhook-settings__list {\r\n  margin-bottom: 12px;\r\n}\r\n\r\n.webhook-settings__actions {\r\n  margin-top: 8px;\r\n}\r\n\r\n.webhook-settings__actions button.primary {\r\n  background-color: var(--color-primary);\r\n  color: var(--color-primary-text);\r\n  border: none;\r\n  border-radius: var(--border-radius-large);\r\n  padding: 8px 16px;\r\n  cursor: pointer;\r\n  font-size: 14px;\r\n}\r\n\r\n.webhook-settings__actions button.primary:hover {\r\n  background-color: var(--color-primary-hover);\r\n}\r\n</style>"],"sourceRoot":""}]);
